<template>
	<div class="base-table">
		<Table stripe border :columns="tableColumn" :data="tableData" :no-data-text="emptyText" :loading="loading"></Table>
		<Page :current="currentPage" :page-size-opts="[10,20,30,40]" :page-size="pageSize" :total="total" @on-change="onCurrentPageChange" @on-page-size-change="onPageSizeChange" size="small" show-elevator show-sizer></Page>
	</div>
</template>

<script>
 	//编辑按钮
	const editButton = (vm, h, currentRow, index) => {
	  return h('Button', {
      props: {
        type: 'primary',
        size: 'small'
      },
      style: {
        marginRight: '10px'
      },
      on: {
        click:()=> {
          vm.$emit('on-edit', currentRow , index)
        }
      }
    }, '编辑')
	}
	// 删除按钮
	const deleteButton = (vm, h, currentRow, index) => {
    return h('Poptip', {
      props: {
        confirm: true,
        title: '您确定要删除这条数据吗?',
        transfer: true
      },
      on: {
        'on-ok': () => {
          vm.$emit('on-delete', currentRow, index)
        }
      }
    }, [
      h('Button', {
        props: {
          type: 'error',
          size: 'small'
        }
      }, '删除')
    ])
	}
	export default {
	  name: 'base-table',
	  props: {
			currentPage: Number, // 当前页
			pageSize: Number, 	// 分页大小
			total: Number, // 分页总数
			loading: Boolean, // 分页加载动画
			columnList: Array, // 表头列，初始需重新生成
      // dataList: Array,
      value: Array  // 通过v-model传来的数据默认绑定在value里，表格数据
	  },
	  data(){
	  	return {
	  		tableColumn: [], // 表头列，最终已重新生产完
				tableData: [], // 表格数据
				emptyText: ' ' // 表格无数据提示
	  	}
	  },
	  created () {
      this.init()
    },
    mounted(){
    	this.$nextTick(()=>{
    	})
    },
    methods:{
    	init(){
    		// console.log(this.columnList)
    		// console.log(this.dataList)
    		// console.log(this.value)
				this.tableData = this.value
    		this.tableColumn = this.columnList.map(item => {
    			if(item.handle){
    				item.render = (h, param) => {
    					let currentRowData = this.tableData[param.index];
    					if(item.handle.length === 2){
    						return h('div', [
  						    editButton(this, h, currentRowData, param.index),
  						    deleteButton(this, h, currentRowData, param.index)
    						]);
    					}else if(item.handle.length === 1){
    						if (item.handle[0] === 'edit') {
  						    return h('div', [
  						      editButton(this, h, currentRowData, param.index)
  						    ]);
    						} else {
  						    return h('div', [
  						      deleteButton(this, h, currentRowData, param.index)
  						    ]);
    						}
    					}
    				} 
    			}
    			return item
				})
				if(!this.tableData.length){
					this.emptyText = '暂无数据'
				}
			},
			// 当前页改变
    	onCurrentPageChange(page){
				this.$emit('on-page-change', page)
			},
			// 每页大小改变
    	onPageSizeChange(size){
				this.$emit('on-size-change', size)
			}
    },
    // 监测父元素里数据的变动更新子组件
  	watch: {
	    value (data) {
	      this.init();
	    }
  	}
	}
</script>

<style lang="less">
	.base-table{
		.ivu-page{
			padding-top: 10px;
			text-align: right;
		}
	}
</style>


