import moment from 'moment'

// 用户管理表头
let userTableColumn =  [
  {
    title: '序号',
    type: 'index',
    align: 'center',
    width: 160,
    key: 'index',
    sortable: true
  },{
    title: '用户名称',
    align: 'center',
    key: 'username'
  },{
    title: '权限',
    align: 'center',
    key: 'auth',
    render: (h, params) => {
    	return params.row.auth == 1?'超级管理员':params.row.auth == 2?'管理员':'用户'
    }
  },{
    title: '头像',
    key: 'avator',
    align: 'center',
    render:(h, params)=>{
      // return params.row.avator ? `<img src='${process.env.BASE_API}/params.row.avator'/>` : '暂无图片'
      return params.row.avator ? h('img', {
        attrs: {
          src: `${process.env.BASE_API}/${params.row.avator}`,
          style: 'width:auto;height:80px;padding-top:5px;'
        }
      }) : '暂无图片'
    }
  },{
    title: '创建时间',
    align: 'center',
    key: 'createTime',
    render:(h, params)=>{
    	return moment(params.row.createTime).format('YYYY-MM-DD HH:mm:ss')
    	// 转时间戳 moment.unix(params.row.createTime).format('YYYY-MM-DD HH:mm')
    }
  },{
    title: '操作',
    key: 'action',
    width: 300,
    align: 'center',
    handle: ['edit', 'delete']
  }
]

// 链接管理表头
let linkTableColumn =  [
  {
    title: '序号',
    key: 'index',
    type: 'index',
    align: 'center',
    width: 200,
    sortable: true
  }, {
    title: '链接名称',
    key: 'name'
  }, {
    title: '类型',
    key: 'type',
    width: 260,
    render: (h, params) => {
      return params.row.type==1?'技术':params.row.type==2?'生活':params.row.type==3?'购物':'时事'
    }
  }, {
    title: '链接地址',
    key: 'address'
  }, {
    title: '创建时间',
    key: 'createTime',
    render: (h, params) => {
      return moment(params.row.createTime).format('YYYY-MM-DD HH:mm:ss')
      // 转时间戳 moment.unix(params.row.createTime).format('YYYY-MM-DD HH:mm')
    }
  }, {
    title: '操作',
    key: 'action',
    width: 300,
    align: 'center',
    handle: ['edit', 'delete']
  }
]

// 标签管理表头
let tagTableColumn = [
  {
    title: '序号',
    key: 'index',
    sortable: true
  }, {
    title: '标签名称',
    key: 'name'
  }, {
    title: '描述',
    key: 'desc'
  }, {
    title: '关联文章数',
    key: 'count'
  }, {
    title: '创建时间',
    key: 'createTime'
  }, { 
    title: '操作',
    key: 'action',
    width: 300,
    align: 'center',
    handle: ['edit', 'delete']
  }
]

// 文章列表表头
let articleTableColumn = [
  {
    title: '序号',
    key: 'index',
    sortable: true
  }, {
    title: '类型',
    key: 'type'
  }, {
    title: '标题',
    key: 'title'
  }, {
    title: '创建时间',
    key: 'createTime'
  }, {
    title: '操作',
    key: 'action',
    width: 300,
    align: 'center',
    handle: ['edit', 'delete']
  }
]

const allTableColumn = {
  userTableColumn,
  linkTableColumn,
  tagTableColumn,
  articleTableColumn
}

export default allTableColumn


