import axios from 'axios'
import {router} from '../router/index.js';
import { Message } from 'iview'

// 创建axios实例
const ax = axios.create({
  baseURL: process.env.BASE_API+'/api', // api的base_url,后面加上'/api'因为后台有设置基础路径
  // baseURL: '/api/api', // 使用代理,注意后台有没有基础路径
	timeout: 10000 // 请求超时时间
})
// axios.defaults.headers.post['Content-Type'] = 'application/json';
// ax.defaults.headers.post['Content-Type'] = 'application/json';

// 请求拦截
ax.interceptors.request.use(config => {
	// 处理请求前逻辑
	if (window.sessionStorage.getItem('token')) {
    config.headers.Authorization = `Bearer ${window.sessionStorage.getItem('token')}`
  }
	return config
}, error => {
	return Promise.reject(error)
}) 

// 正常响应走response，抛出的异常走error
ax.interceptors.response.use( response => {
  // console.log(response)
  if(response.status !== 200){
    router.replace({
      path: '/login',
      query: {redirect: router.currentRoute.fullPath}
    })
  }
  // 异常请求处理
  // switch(response.data.code){
  //   case 401:
  //     router.replace({
  //       path: '/login',
  //       query: {redirect: router.currentRoute.fullPath}
  //     })
  //   case 404:
  //     router.replace({
  //       path: '/login',
  //       query: {redirect: router.currentRoute.fullPath}
  //     })
  //   case 405:
  //     router.replace({
  //       path: '/login',
  //       query: {redirect: router.currentRoute.fullPath}
  //     })
  //   case 500:
  //     router.replace({
  //       path: '/login',
  //       query: {redirect: router.currentRoute.fullPath}
  //     })
  // }
  return response
}, error => {
	/**
	  * 下面的注释为通过response自定义code来标示请求状态，当code返回如下情况为权限有问题，登出并返回到登录页
	  * 如通过xmlhttprequest 状态码标识 逻辑可写在下面error中
	**/
  //  const res = response.data;
  //     if (res.code !== 20000) {
  //       Message({
  //         message: res.message,
  //         type: 'error',
  //         duration: 5 * 1000
  //       });
  //       // 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
  //       if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
  //         MessageBox.confirm('你已被登出，可以取消继续留在该页面，或者重新登录', '确定登出', {
  //           confirmButtonText: '重新登录',
  //           cancelButtonText: '取消',
  //           type: 'warning'
  //         }).then(() => {
  //           store.dispatch('FedLogOut').then(() => {
  //             location.reload();// 为了重新实例化vue-router对象 避免bug
  //           });
  //         })
  //       }
  //       return Promise.reject('error');
  //     } else {
  //       return response.data;
  //     }
  console.log(error)
  Message.error('登录异常，请重新登录！')
  router.replace({
    path: '/login',
    query: {redirect: router.currentRoute.fullPath}
  })
	return Promise.reject(error)
})

export default ax


